type $Fixed = any

export default {
  date: new Date(),
  getTimeTitle() {
    const hour: number = this.date.getHours()
    const timeDom: Element = document.querySelector('#timeTitle')
    let timeTile: string = ''
    if (hour > 8 && hour <= 12) {
      timeTile = 'Good morning ðŸŒ»'
    } else if (hour > 12 && hour <= 19) {
      timeTile = 'Good afternoon ðŸŒž'
    } else if (hour > 19 && hour <= 23) {
      timeTile = 'Good night ðŸ§Ÿ'
    } else {
      timeTile = '(ï½žï¹ƒï½ž)~zZ ðŸ·'
    }
    timeDom.innerText = timeTile
  },
  progressOfYear() {
    const isLeapYear = (n: number) => {
      return (n % 4 === 0 && n % 100 !== 0) || n % 400 === 0
    }

    const year: number = parseInt(this.date.getFullYear())
    const timeDom: Element = document.querySelector('#timeYear')
    timeDom.innerText = year.toString()

    const time: number = Date.parse(this.date)
    const lasttime: number = Date.parse(`${year}-01-01`)
    const day: number = (time - lasttime) / (1000 * 60 * 60 * 24)

    const allDay: number = isLeapYear(year) ? 366 : 365
    const percentYear = Math.round((day / allDay) * 100)

    const progressDom: $Fixed = document.querySelector(".wired-progress")
    const percentDom: Element = document.querySelector('#percentTime')
    progressDom.value = percentYear.toString()
    percentDom.innerText = percentYear.toString()
  }
}
